import axios from './axios'
import { baseUrl} from '../../public/environment'

export default class PublicFn {
  getUrl(url){
     return `${baseUrl}${url}`; //  __ce.baseURL
  };
  //ajax;post
  postServer(opt) {
    let data = {};
    if (opt.data) {
      data = opt.data;
    }
    
    axios.post(opt.url, data).then((res) => {
      if (opt.onSuccess) {
        if(!res.data.status){ // statusfalse
          
          if (opt.onFailed) { // 
            opt.onFailed(res);
          }
          return;
        }
        opt.onSuccess(res.data); // 
      }
    }).catch(error => {
      console.log(error)
    });
  }

  //ajax;get
  getServer(opt) {
    let data = {};
    if (opt.data) {
      data = opt.data;
    }
    // axiosgetpost
    axios.get(opt.url, {params: data}).then((res) => {
      if (opt.onSuccess) {
        if(!res.data.status){

          if (opt.onFailed) {
            opt.onFailed(res);
          }
          return;
        }
        opt.onSuccess(res.data);
      }
    }).catch(error => {
      console.log(error)
    });
  }

  setData(opt) {
    let data = {};
    if (opt.data) {
      data = opt.data;
    }
    return data;
  }
}